import {
  TemplateResult,
  css,
  customElement,
  html,
  ifDefined,
  property,
  repeat,
  state,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import {
  LicensesService,
  ProductLicenseValidationStatusViewModel,
} from "@umbraco-licenses/generated";
import { tryExecute } from "@umbraco-cms/backoffice/resources";
import { UMB_NOTIFICATION_CONTEXT } from "@umbraco-cms/backoffice/notification";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";

const elementName = "umb-license-card";

@customElement(elementName)
export class LicenseCardElement extends UmbLitElement {
  #notificationContext?: typeof UMB_NOTIFICATION_CONTEXT.TYPE;

  @property({ type: Object })
  model?: ProductLicenseValidationStatusViewModel;

  @property()
  applicationUrl?: string;

  @state()
  private _loading = false;

  @state()
  private _features: Array<{ name: string; active: Boolean }> = [];

  @state()
  private _variables: Array<{ name: string; value: string }> = [];

  #currentUrl?: string;
  #errorMessage?: string;

  connectedCallback(): void {
    super.connectedCallback();

    this.consumeContext(UMB_NOTIFICATION_CONTEXT, (instance) => {
      this.#notificationContext = instance;
    });

    this._features = Object.entries(this.model?.features ?? {}).map((x) => ({
      name: x[0],
      active: x[1],
    }));

    this._variables = Object.entries(this.model?.variables ?? {}).map((x) => ({
      name: x[0],
      value: x[1],
    }));

    this.#localizeDates();
    this.#localizeError();
    this.#setCurrentUrl();
  }

  #setCurrentUrl() {
    this.#currentUrl = this.model?.domains?.find((d) =>
      d.startsWith("*")
        ? this.applicationUrl?.endsWith(d.substring(1))
        : this.applicationUrl === d
    );
  }

  #localizeError() {
    if (this.model?.lastResult?.startsWith("Valid")) {
      return;
    }

    this.#errorMessage = this.localize.term(
      `licenses_validation${this.model?.lastResult}`,
      [this.model?.productId]
    );
  }

  #localizeDates() {
    ["lastValidatedOn", "lastSuccessfullyValidatedOn", "expiresOn"].forEach(
      (key) => {
        if (!this.model?.[key]) return;

        if (!this.model?.[key].endsWith("Z")) {
          this.model[key] += "Z";
        }

        this.model[key] = this.localize.date(this.model[key], {
          dateStyle: "medium",
          timeStyle: "short",
        });
      }
    );
  }

  async #requestValidation() {
    this._loading = true;

    const { data } = await tryExecute(
      this,
      LicensesService.postUmbracoBackofficeApiLicensesValidate({ body: { productId: this.model?.productId } }),
      {
        disableNotifications: true,
      }
    );

    this._loading = false;

    if (!data.license) {
      return;
    }

    if (data.success) {
      this.model = { ...this.model, ...data.license };
      this.#localizeDates();
    }

    this.#notify(data.status, data.success);
  }

  #notify(status?: string | null, success?: boolean | null) {
    if (!status) return;

    this.#notificationContext?.peek(success ? "positive" : "danger", {
      data: {
        message: this.localize.term(`licenses_validation${status}`, [
          this.model?.productId,
        ]),
      },
    });
  }

  #renderSection(
    labelKey: string,
    labelDefault: string,
    value?: TemplateResult | string | null,
    id?: string
  ) {
    return html`
      <div id=${ifDefined(id)}>
        <dt><umb-localize .key=${labelKey}>${labelDefault}</umb-localize></dt>
        <dd>${value}</dd>
      </div>`;
  }

  render() {
    return html`
      <uui-box ?valid=${!this.#errorMessage}>
        <span slot="headline">${this.model?.productId}</span>
        <uui-button
          slot="header-actions"
          id="validateBtn"
          look="secondary"
          compact
          @click=${this.#requestValidation}
          label=${this.localize.term("licenses_validate")}
          .state=${this._loading ? "waiting" : undefined}
        ></uui-button>
        <dl>
          ${when(
            this.model?.licenseKey,
            () => this.#renderSection("licenses_licenseKey", "License key", this.model?.licenseKey)
          )}
          ${when(
            this.model?.domains?.length,
            () => this.#renderSection("licenses_licensedDomains", "Licensed domains", html`
              <ul>
                ${repeat(this.model?.domains ?? [],
                  (domain) => domain,
                  (domain) => html`
                    <li>
                      <span>
                        ${domain}
                        ${when(
                          domain === this.#currentUrl,
                          () => html`<uui-tag color="positive"><umb-localize key="general_current">Current</umb-localize></uui-tag>`
                        )}
                      </span>
                    </li>
                  `
                )}
              </ul>
            `,
            "domains")
          )}
          ${when(
            this._features.length,
            () => this.#renderSection("licenses_features", "Features", html`
              <ul>
                ${repeat(this._features,
                  (feature) => feature.name,
                  (feature) => html`
                    <li class="feature-${feature.active ? "active" : "inactive"}">
                      ${feature.name}
                      <uui-icon
                        .name=${feature.active ? "umb:check" : "umb:delete"}
                        class="license-icon color-${feature.active ? "green" : "red"}"
                        ></uui-icon>
                    </li>
                  `
                )}
              </ul>
            `,
            "features")
          )}
          ${when(
            this._variables.length,
            () => this.#renderSection("licenses_variables", "Variables", html`
              <ul>
                ${repeat(this._variables,
                  (variable) => variable.name,
                  (variable) => html`<li>${variable.name}: ${variable.value}</li>`
                )}
              </ul>
            `,
            "variables")
          )}
          ${when(
            this.model?.tier,
            () => this.#renderSection("licenses_tier", "Tier", this.model?.tier)
          )}
          ${when(
            this.model?.expiresOn,
            () => this.#renderSection("licenses_expiresOn", "Expires on", this.model?.expiresOn)
          )}
          ${when(
            this.model?.lastValidatedOn,
            () => this.#renderSection("licenses_lastValidated", "Last validated", this.model?.lastValidatedOn)
          )}
          ${when(
            this.model?.lastSuccessfullyValidatedOn,
            () => this.#renderSection("licenses_lastSuccessfullyValidated", "Last successfully validated", this.model?.lastSuccessfullyValidatedOn)
          )}
          ${when(
            this.#errorMessage,
            () => this.#renderSection("general_error", "Error", this.#errorMessage)
          )}
        </dl>
      </uui-box>
    `;
  }

  static styles = [
    css`
      :host {
        display: flex;
      }

      uui-box {
        --uui-color-divider-standalone: var(--uui-color-danger);

        width: 100%;
        border: 1px solid var(--uui-color-danger);
        box-shadow: 0 1px 3px rgba(191, 33, 78, 0.12),
          0 1px 2px rgba(191, 33, 78, 0.24);
      }

      uui-box[valid] {
        --uui-color-divider-standalone: var(--uui-color-positive);

        border-color: var(--uui-color-positive);
        box-shadow: 0 1px 3px rgba(25, 134, 74, 0.12),
          0 1px 2px rgba(25, 134, 74, 0.24);
      }

      ul,
      dl {
        margin: 0;
      }

      dl {
        list-style: none;
      }

      dl > div {
        display: flex;
        column-gap: var(--uui-size-3);
      }

      dl > div + div {
        margin-top: var(--uui-size-4);
      }

      dt {
        font-weight: 700;
        line-height: 1.3;
        min-width: 100px;
        max-width: 100px;
      }

      dd {
        word-break: break-word;
        line-height: 1.3;
      }

      dd ul {
        padding-left: 0;
      }

      li {
        overflow: hidden;
        text-overflow: ellipsis;
      }

      li span {
        white-space: nowrap;
        display: flex;
        align-items: center;
        gap: var(--uui-size-2);
      }

      .license-icon {
        width: 1.4em;
        height: 1.4em;
      }

      .license-icon[name="umb:alert"] {
        color: var(--uui-color-danger-standalone);
      }

      uui-tag {
        --uui-type-small-size: 10px;
        --uui-size-space-1: 0;
        padding: 0 2px;
        text-transform: uppercase;
      }

      #domains,
      #features,
      #variables {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }

      #domains dd,
      #features dd,
      #variables dd {
        overflow: hidden;
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: LicenseCardElement;
  }
}
