import {
  css,
  customElement,
  html,
} from "@umbraco-cms/backoffice/external/lit";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";

const elementName = "umb-licenses-dashboard";

@customElement(elementName)
export class LicensesDashboardElement extends UmbLitElement {
 
  render() {
    return html`
      <uui-box .headline=${this.localize.term("licenses_registeredLicenses")}>
        <umb-localize key="licenses_registeredLicensesDescription"
          >Allows you to view the details of each license and provides tools for
          requesting revalidation.</umb-localize
        >
        <umb-licenses-grid></umb-licenses-grid>
      </uui-box>
    `;
  }

  static styles = [
    css`
      :host {
        display: block;
        padding: var(--uui-size-layout-1);
      }
    `,
  ];
}

export default LicensesDashboardElement;

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: LicensesDashboardElement;
  }
}
