import { tryExecute } from "@umbraco-cms/backoffice/resources";
import {
  css,
  customElement,
  html,
  state,
  when,
} from "@umbraco-cms/backoffice/external/lit";
import { LicensesService } from "@umbraco-licenses/generated";
import { UmbLitElement } from "@umbraco-cms/backoffice/lit-element";

const elementName = "umb-licenses-grid";

@customElement(elementName)
export class LicensesGridElement extends UmbLitElement {
  @state()
  _licenses: Array<Record<string, any>> = [];

  @state()
  _loaded = false;

  #applicationUrl?: string;

  async connectedCallback() {
    super.connectedCallback();

    const { data, error } = await tryExecute(
      this,
      LicensesService.getUmbracoBackofficeApiLicensesAll(),
      {
        disableNotifications: true,
      }
    );

    if (error) {
      this._loaded = true;
      return;
    }

    this._licenses = data.licenses;
    this.#applicationUrl = data.applicationUrl;
    this._loaded = true;
  }

  render() {
    return html`
      ${when(
        !this._loaded,
        () => html`<div class="relative" style="grid-column: 1 / 5; margin-top: var(--uui-size-5);"><uui-loader></uui-loader></div>`,
        () => html`
          <div>
            <umb-localize
              key="licenses_validationUrl"
              .tokens=${[this.#applicationUrl]}
              >Application URL used in validation request</umb-localize
            >: <strong>${this.#applicationUrl}</strong>
          </div>
          ${when(
            this._licenses.length,
            () => html`
              <div id="grid">
              ${this._licenses.map((license) => html`
                <umb-license-card
                  .model=${license}
                  .applicationUrl=${this.#applicationUrl}
                ></umb-license-card>`
              )}
              </div>
            `,
            () => html`
              <umb-empty-state>
                <umb-localize key="content_listViewNoItems">There are no items show in the list.</umb-localize>
              </umb-empty-state>
            `
          )}
        `
      )}
    `;
  }

  static styles = [
    css`
      :host {
        container-type: inline-size;
        display: block;
      }

      #grid {
        --column-count: 1;
        display: grid;
        grid-gap: var(--uui-size-7);
        grid-template-columns: repeat(var(--column-count), minmax(0, 1fr));
        margin-top: var(--uui-size-layout-1);
      }

      @container (min-width: 800px) {
        #grid {
          --column-count: 2;
        }
      }

      @container (min-width: 1100px) {
        #grid {
          --column-count: 3;
        }
      }

      @container (min-width: 1500px) {
        #grid {
          --column-count: 4;
        }
      }

      umb-empty-state {
        margin: var(--uui-size-layout-3) 0 var(--uui-size-layout-2);
      }
    `,
  ];
}

declare global {
  interface HTMLElementTagNameMap {
    [elementName]: LicensesGridElement;
  }
}
